:root {
  --my-bg-color: black;
  --my-color: rgb(54, 230, 48);
}

body {
  
  background-color: var(--my-bg-color);
  max-width: 1100px; margin: 0 auto; 
  
}

h1 {
  font-weight: 550;
  font-size: 18px;
}

nav {
  padding: 2% 3% 2% 3%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Space Mono', monospace;
  width: 100%;
  color: var(--my-color);
}

nav div {
  display: flex;
  width: 270px;
}

.datos {
  padding-left: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.datos div:first-child {
  font-weight: bold;
}

.redes {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

i {
  color: var(--my-color);
}

.fotoperfil {
  border: 3px solid var(--my-color);
  border-radius: 10px;
  height: 180px;
}

.custom-control-input:checked ~ .custom-control-label::before {
  color: #fff;
  border-color: var(--my-color);
  background-color: var(--my-color);
}

@media (max-width: 820px) {
  nav {
    padding: 2% 2% 2% 2%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    font-family: 'Space Mono', monospace;
    width: 100%;
  }
  nav div {
    margin-top: 2%;
    display: flex;
    width: 200px;
  }
}

.lenguajes {
  margin-top: 20px;
  color: var(--my-color);
  font-family: 'Space Mono', monospace;
  padding: 2% 5% 2% 5%;
  text-align: center;
}

.lenguajes p {
  margin-top: 5%;
}

.portfolio {
  margin-top: 20px;
  color: var(--my-color);
  font-family: 'Space Mono', monospace;
  padding: 2% 5% 2% 5%;
  text-align: center;
}